<!--  -->

<template>
<div class='wrapper'>
 <v-head></v-head>
  <div class="clear"></div>
 <div class="el-main">
     <div id="infoform">
<el-form id="form1" :inline="true" :rules="rules" :model="form1" ref="form1" label-width="100px" >
    <el-row style="width:1400px">    
    <el-form-item label="姓名：" prop="s_name">
        <el-input v-model="form1.s_name" type="text" style="width:100%"></el-input>
     </el-form-item>
	</el-row>
	 <el-row style="width:1400px">    
    <el-form-item label="专业编号：" prop="smajorID">
        <el-input v-model="form1.smajorID" style="width:100%"></el-input>
     </el-form-item>
	 </el-row>
	 <el-row style="width:1400px">    
    <el-form-item label="班级：" prop="sclass">
        <el-input v-model="form1.sclass" style="width:100%"></el-input>
     </el-form-item>
	 </el-row>
	  <el-row style="width:1200px">    
         <el-form-item label="入学时间：" prop="stime_of_enrollment">
        <el-input v-model="form1.stime_of_enrollment" style="width:100%"></el-input>
     </el-form-item>
	 </el-row>

<el-col></el-col>
</el-form>
<el-form id="form2" :inline="true" label-width="150px" >
<el-form-item>
        <el-button type="primary" @click="sumitForm('form1')">保存</el-button>
                 <el-button type="success" @click="resetForm('form1')">重置</el-button>
                 <el-button @click="exit('form1')">返回</el-button>
     </el-form-item>
    
     </el-form>
        </div>
      </div>
    </div>

</template>
    
<script>
 import vHead from '../commons/AdminstratorNav.vue';
export default {
  components:{
      vHead,
  },
  data(){
  
      return{
          hostUrl:'/student',
          check1: false,
          pickerOptions: {
         
        },
          form1:{
              
              s_name: '',
              smajorID:'',
              sclass:'',
              stime_of_enrollment:'',
          },
		  rules: {
          s_name: [
            { required: true, message: '输入名字', trigger: 'blur' },
          ],
           smajorID: [
            { required: true, message: '请输入专业编号', trigger: 'blur' },
            { min: 5, max: 12, message: '长度为12个字符', trigger: 'blur' }
          ],
          sclass:[
               { required: true, message: '请输入学生班级', trigger: 'blur' }
          ],
         stime_of_enrollment:[
             { type: 'date', required: true, message: '请选择时间', trigger: 'change' },
         ]

        }
      };
  },
  methods:{
        sumitForm(formName) {
       console.log(this.form1.s_name,this.form1.smajorID,this.form1.sclass,this.form1.stime_of_enrollment);
       var vm=this;
       $.ajax({
         type:'post',
         dataType:'json',
         url:'/administor/addStudent',
         data:{
            "name":this.form1.s_name,
            "majID":this.form1.smajorID,
            "claID":this.form1.sclass,
            "year":this.form1.stime_of_enrollment
         },
         success:function(response){
           if(response.data===1){
             vm.$message({
               type:'success',
               message:'添加成功'
             })
           }else{
             vm.$message({
               type:'info',
               message:'添加失败'
             })
           }
         },
         error:function(err){
           console.log(err);
           console.log("error"); 
           vm.$message({
               type:'warning',
               message:'网络故障'
             })
         }
       })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      exit(formName) {
      localStorage.setItem("first","1");
      console.log(localStorage)
        this.$router.push('/adminstrator/studentInfo')
      }
    }
  }
</script>
<style scoped>
.clear{
  clear: both;
}
.el-main{
    margin-top:3px;
    background-color:#eef1f6;
    color: #333;
    text-align: center;
    height:800px;
}
#infoform{
    padding-top: 60px;
}
#form1{
    border:1px solid #eef1f6;
    width:400px;
    margin:10px auto;
}
#form2{
    width:220px;
    margin:0px auto;
    float: center;
}
  .el-form-item{
      float:left;
  }
</style>


